<template>
  <div>

    <h1 class="font-bold font20 marginB10">{{coin}}认购</h1>
    <div class="space30"></div>

    <div class="BuyForm flex align center">

      <div class="mainBox">


        <div class="form">

          <div class="innerBox flex between align">

            <div class="inner">
              <div class="tag flex align">
                <div>认购额度 <b class="color-green">(可用{{coinBalance}})</b></div>
                <div class="linkText font-bold marginL10" @click="params.fromNumber = coinBalance">全部投入</div>
              </div>
              <div class="inputGroup flex align">
                <div class="coin flex align"><i class="iconImg" :class="coin"></i><b>{{coin}}</b></div>
                <input type="text" placeholder="请输入认购额度" v-model="params.fromNumber">
              </div>
            </div>
            <!-- inputGroup -->

            <!-- <div class="chooseItem flex align between">
              <span :class="{active:currSelect==item}" v-for="(item,index) in selectItem" :key="index" @click="currSelect = item">{{item}}%</span>
            </div> -->

            <i class="exchangeIcon el-icon-sort"></i>


            <div class="inner">
              <div class="tag">可获得WMC额度</div>
              <div class="inputGroup flex align">
                <input type="text placeholder" disabled v-model="ableGetNum" :placeholder="`可获得额度${ableGetNum}`">
                <div class="coin left flex align"><i class="iconImg xcion"></i><b>WMC</b></div>
              </div>
            </div>
            <!-- inputGroup -->
          </div>
          <!-- innerBox -->


          <div class="themBtn" @click="buySend" v-loading="load">立即认购</div>

          <div class=" textCenter">
            <div class="agreement">
              <el-checkbox v-model="checked"></el-checkbox>&nbsp;&nbsp;&nbsp;阅读并同意<a href="">《认购协议》</a>
            </div>
          </div>
          <!-- textCenter -->

        </div>
        <!-- form -->

      </div>
      <!-- mainBox -->

    </div>
    <!-- BuyForm -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapActions,
    mapState
  } from 'vuex'

  import {
    orderBuy
  } from "../../data/apis";

  export default {
    props: ['id', 'coin'],
    data() {
      return {
        params: {
          productId: null,
          fromNumber: null
        },
        load: false,
        checked: false,

        currSelect: 25,
        selectItem: [25, 50, 75, 100]

      }
    },
    computed: {
      ...mapState(['userInfo']),
      coinBalance() {
        return this.userInfo.balanceModels.find(x => x.coin.coinName == this.coin).amount || '0.0000';
      }, //user
      coinPrice() {//获取价格
        return this.userInfo.balanceModels.find(x => x.coin.coinName == this.coin).coin.coinPrice;
      }, //coinPrice
      WMCPrice(){
        return this.userInfo.balanceModels.find(x => x.coin.coinName == 'WMC').coin.coinPrice;
      },
      ableGetNum(){//可获得额度计算
        if(this.params.fromNumber){
            let usdtNum = this.params.fromNumber * this.coinPrice;//当前输入的价值usdt价格
            return this.toFixed_num((usdtNum / this.WMCPrice) , 8);//可获得的数量
        }else{
          return ''
        }
      }//ableGetNum
    },
    methods: {
      ...mapActions(['actionUserInfo']),
      buySend() { //购买

        if (!this.params.fromNumber) {
          return this.$message.error('请输入认购额度');
        }
        if (!this.checked) {
          return this.$message.error('请阅读并同意《认购协议》');
        }

        this.load = true;
        orderBuy(this.params).then(v => {
          this.buyOk()
        }).catch(e => {
          this.load = false;
        })
      }, //buySend
      buyOk() { //购买完成
        this.load = false;
        this.$confirm(`已成功认购 ${this.params.fromNumber} ${this.coin} ！`, '认购成功', {
          confirmButtonText: '确定',
          showCancelButton: false,
          type: 'success'
        }).then(v => {
          this.params.fromNumber = null;
          this.actionUserInfo();
        })
      } //buyOk
    },
    mounted() {
      this.params.productId = this.id;
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .BuyForm {
    top: 0;
    left: 0;

    .exchangeIcon {
      background: $them_color;
      color: white;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      border-radius: 100px;
      transform: rotate(90deg);
      -webkit-transform: rotate(90deg);
      margin: 20px;
      box-shadow: 0px 0px 15px #79a1ff;
      position: relative;
      top: 13px;
    }

    .mainBox {
      width: 100%;

      .title {
        h2 {
          font-size: 20px;
          font-weight: bold;
        }
      }

      .info {
        margin-top: 30px;
        margin-bottom: 50px;

        .dataGroup {
          padding-left: 30px;
          border-left: 1px solid #E5E5E5;
          margin-left: 30px;

          >.item {
            width: 33%;
            margin: 10px 0;

            b {
              display: block;
              font-size: 18px;
              font-weight: normal;
            }

            span {
              font-size: 14px;
            }

            small {
              color: $them_color_gray_light;
              font-size: 12px;
            }
          }
        }
      }

      .inputGroup {
        // background: #f6f6f6;
        border: 1px solid #E9E9E9;
        border-radius: 2px;
        flex: 1;

        .coin {
          min-width: 140px;
          padding: 10px;
          height: 60px;
          border-right: 1px solid #E9E9E9;
          &.left{
            border-left: 1px solid #E9E9E9;
          }

          .iconImg {
            margin-right: 10px;
          }

          b {
            font-size: 18px;
            font-weight: bold;
          }
        }

        input {
          border: none;
          background: none;
          padding: 10px 20px;
          font-size: 16px;
          flex: 1;
          height: 60px;
        }
      }

      .chooseItem {
        margin-bottom: 30px;
        margin-top: 10px;
        padding-left: 25px;

        span {
          width: 25%;
          margin: 10px;
          border: 1px solid #D4D4D4;
          text-align: center;
          color: $them_color_gray_light;
          padding: 5px;
          cursor: pointer;
          border-radius: 2px;

          &.active {
            background: $them_color;
            color: white;
            border: 1px solid $them_color;
          }
        }
      }

      .themBtn {
        margin: 30px auto;
        width: 50%;
      }

      .agreement {
        color: $them_color_gray_light;
        text-align: center;
        display: inline-block;

        a {
          color: $them_color;
          text-decoration: underline;
        }
      }

      .tag {
        margin-bottom: 15px;
        font-size: 16px;

        &.red {
          color: #D54F4F;
        }

        &.green {
          color: #33CC1F;
        }
      }

      .innerBox {
        >div {
          flex: 1;
        }
      }

      /*.innerBox{
        position: relative;
        &::after{
          content: '';
          left: 5px;
          border-left: 1px dotted rgba(71,118,230,0.5);
          height: 100px;
          top: 60px;
          position: absolute;
        }
      }*/
    }
  }

</style>



