<template>
  <div class="reLogin">
    <h1> <i class="el-icon-warning"></i> <span>请登录账号后认购</span></h1>
    <div class="textCenter btnBox">
      <div class="login hoverTr3" @click="gopage('/login')">登录</div>
      <div class="register hoverTr3" @click="gopage('/register')">注册</div>
    </div>
  </div>
</template>

<script>
  import {
    mapMutations,
    mapActions,
    mapState
  } from 'vuex'

  export default {
    props: [],
    data() {
      return {

      }
    },
    computed: {
      ...mapState(['userInfo']),
    },
    methods: {
      ...mapActions(['actionUserInfo']),
    },
    mounted() {

    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .reLogin {
    text-align: center;
    h1{
      i{
        display: block;
        font-size: 50px;
        color: $them_color_warning;
      }
      span{
        font-size: 20px;
        color: $them_color_gray;
      }
    }
    .btnBox{
      margin-top: 40px;
      >div{
        color: $them_color;
        border: 1px solid $them_color;
        cursor: pointer;
        &.register{
          background: $them_color;
          color: white;
        }
        border-radius: 4px;
        display: inline-block;
        padding: 10px 40px;
        margin: 0 10px;
      }
    }
  }

</style>



