<template>
  <div id="exchange">

    <div :class="{filter:showForm}">
      <Nav page="exchange" />

      <Banner text="认购产品"/>

      <!-- 正文内容 -->

      <div class="space40"></div>


      <div class="mainContent animated fadeIn" v-if="item">


        <div class="shadownItemBox">

          <ProItem :item="item" :detail="true"/>

        </div>
        <!-- proBox -->

      </div>
      <!-- mainContent -->


     

      <div class="shadownItemBox animated fadeIn" v-if="userInfo && item">
         <BuyForm  :id="item.id" :coin="item.productName"/>
      </div>

      <div class="shadownItemBox animated fadeIn" v-else>
        <requireLogin/>
      </div>

      

      <div class="shadownItemBox animated fadeIn">
        <h1 class="font-bold font20 marginB10">活动说明</h1>

        <el-tag type="danger" size="small">认购折扣：X 折</el-tag>
        <p>   &nbsp</p>
        <p>如认购 1 ETH ，认购折扣为9折，则实际扣款额度为0.9 个ETH</p>
<p>   &nbsp</p>
         <el-tag type="success" size="small">认购返利：X %</el-tag>
        <p>   &nbsp</p>
        <p>如认购 1 ETH ，获得 1000 WMC ，认购返利为 10% ，则实际获得为 1100 WMC</p>
      </div>

       <div class="space50"></div>

      <!-- 正文结束 -->
      <Footer />
    </div>
    <!-- filter -->

  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import Nav from './nav'
  import Banner from '@com/exchange/banner'
  import BuyForm from '@com/exchange/buyForm'
  import requireLogin from '@com/exchange/requireLogin'
  import Footer from './footer'
  import ProItem from '@com/exchange/proItem'


  export default {
    data() {
      return {
        showForm: false,
        item:null
      }
    },
    components: {
      Nav,
      Banner,
      Footer,
      requireLogin,
      BuyForm,
      ProItem
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setUserInfo']),
      ...mapActions(['actionUserInfo']),

    },
    mounted() {
      this.item = this.$route.query;
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .mainContent {
    >.title {
      margin-top: 50px;
      margin-bottom: 40px;

      h2 {
        font-size: 30px;
        font-weight: bold;
      }

      .help {
        color: $them_color_gray_light;
        font-size: 16px;

        i {
          display: inline-block;
          width: 25px;
          height: 25px;
          vertical-align: middle;
          margin-top: -2px;
          background: url('../assets/img/help@2x.png') center no-repeat;
          background-size: 100% 100%;
        }
      }
    }

    .proItem {
      padding: 0 0;
      overflow: hidden;

      .img {
        width: 400px;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }

      .rightContent {
        flex: 1;
        padding: 20px 50px;

        .pair {
          .row {
            background: url('../assets/img/rightG.png') center no-repeat;
            background-size: 100% auto;
            width: 60px;
            height: 30px;
            margin: 0 25px;
          }

          .iconImg {
            width: 35px;
            height: 35px;
          }

          span {
            font-size: 25px;
            font-weight: 500;
            margin-left: 10px;
          }
        }

        .goBtn {
          border: 1px solid $them_color;
          display: block;
          cursor: pointer;
          color: $them_color;
          font-size: 16px;
          padding: 10px 20px;
          font-weight: 600;
          border-radius: 2px;
          font-weight: normal;

          i {
            display: inline-block;
            width: 25px;
            height: 15px;
            background: url('../assets/img/right@2x.png') center no-repeat;
            background-size: 100% auto;
            position: relative;
            top: 1px;
            margin-left: 10px;
          }
        }

        .pText {
          color: $them_color_gray_light;
          font-size: 14px;
          display: block;
          padding: 30px 0;
          border-bottom: 1px solid #E9E9E9;
          margin-bottom: 10px;
        }

        .dataGroup {
          .item {
            width: 33%;
            margin-top: 20px;

            b {
              display: block;
              font-size: 20px;
              font-weight: normal;

              span {
                font-size: 14px;
              }
            }

            small {
              color: $them_color_gray_light;
              font-size: 14px;
            }
          }
        }
      }
    }
  }

</style>



